.banner{
    width: 70%;
    
    

}
.mob{
    display: none;
}
.web{
    display: block;
}
.banner_text{
    position: absolute;
    color: aliceblue;
    line-height: 1;
    top:  84%;
    left: 14.5%;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.head{
    color: grey;
    position: absolute;
    top:  79%;
    left: 14.5%;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 300;
}
.pf{
    color: white;
    padding: 18px;
}
.btn{
    background-color: #e50914;
    height: 50px;
    width: 150px;
    border: 1px solid red;
    border-radius: 2%;
    color: aliceblue;
    font-size: 20px;
    font-family: Arial, Helvetica, sans-serif;

}
@media (max-width:768px){
    
    .mob{
      display: block;
      width: 140%;
    }
    .banner_text{
        position: relative;
        text-align: center;
        left: 53%;
        margin-top: 15px;
    }
    .btn{
        height: 30px;
        width: 85px;
        border-radius: 3%;
    }
    .head{
        display: none;
    }
    .web{
      display: none;
    }
}