.cards{
  width: 100%;
  height: 100%;
}
.card{
  margin: 0.5%;
  width:18%;
  border-radius: 12px;
  float: left;
}
.card:hover{
  transform: scale(1.06,1.06);
}
.card_img{
  width: 100%;
  height: 250px;
}
