.nav{
    position: fixed;
    top: 0;
    width: 100%;
    background-color: #111;
    height: auto;
    display: flex;
    justify-content: space-between;
    
}

.nav_logo {
    width: 155px;
    object-fit: contain;
    padding-left: 30px;
}

.avt_logo{
    width: 50px;
    object-fit: contain;
    
}
.login{
    background-color: #e50914;
    height: 40px;
    width: 100px;
    margin: 15px;
    border: 1px solid red;
    border-radius: 2%;
    color: aliceblue;
    font-size: 20px;
    font-family: Arial, Helvetica, sans-serif;
}

@media (max-width:768px){
    .login{
        height: 30px;
        width: 85px;
        border-radius: 3%;
    }
}